import React, { useEffect, useState } from 'react';
import  Icon from '@iconify/react';
import delete24Regular from '@iconify-icons/fluent/delete-24-regular';
import bxsEdit from '@iconify-icons/bx/bxs-edit';
import refreshIcon from '@iconify-icons/el/refresh';
import searchIcon from '@iconify-icons/carbon/search';



function Table(){

const [getData,setData]=useState([])
const [postData1,setPostData]=useState("");
const [postData2,setPostData1]=useState("");


function changeHandler1(event){
  setPostData(event.target.value);}


function changeHandler2(event){
  setPostData1(event.target.value);}

const handleDelete=(e,post)=>{
  fetch("",{method:"DELETE"})
  .then(()=>alert("Deleted SuccessFully"))
}

const handleEdit=(e,post)=>{

  setPostData(post.title);
 setPostData1(post.id);;
  fetch("",{
    method:"PUT",
    headers:{'content-type':'application/json'},
    body:JSON.stringify(post)})
    .then(response=>response.json())
    .then(data=>console.log(post.id))
}


const submitHandler=(e)=>{
  e.preventDefault();
  const postData={GroupName:postData1,IsActive:postData2};
  fetch("https://jsonplaceholder.typicode.com/posts",{
    method:"POST",
    headers:{'content-type':'application/json'},
    body:JSON.stringify(postData)
  }).then(result=>result.json())
  .then((response)=>console.log(response))
}


useEffect(()=>{
  fetch("https://jsonplaceholder.typicode.com/todos")
  .then(response=>response.json())
  .then(data=>setData(data))}
  ,[]
)


const resetHandler=()=>{
  setPostData("");setPostData1("");
}
  
function refreshPage() {
  window.location.reload();
}





  return( <>
    <div className="mainFormD">
<form  onSubmit={submitHandler} >
  <div className="form_D">

<div>
 <p> Standard Name</p> <input className="GroupNameD" id="GroupName" value={postData1} onChange={changeHandler1} type="text" name="GroupName1"/>
  </div>
<div>
 <p>Standard Abbreviation</p> <input className="GroupNameD" id="GroupName" value={postData2} onChange={changeHandler2} type="text" name="GroupName1"/>
  </div>
  {/* <div>
  <label for="Active">Active</label>
  <input type="checkbox" className="ActiveCheckK"  value={postData2} onChange={changeHandler2} id="Active" name="IsActive" />
  </div> */}
  
  <div className="btnD">
  <div>
  <button className="resetBtnD" onClick={resetHandler}>Reset</button>
  <button className="saveBtnD"  >Save</button></div></div>

</div>
</form>  
</div>




<div className="TableHeadB">
<p>Total Record</p>
<select className="GroupSelectB" id="GroupSelect" name="GroupName" >
        <option value="" disabled selected hidden>--Select--</option>
        <option value="">Mock Test Name</option>
        <option value="">Last Year Paper</option>
      </select>
<select className="ExactlySelectB" id="ExactlySelect" name="Exactly" >
        <option value="" disabled selected hidden>--Select--</option>
        <option value="">Exactly</option>
        <option value="">d2</option>
      </select>
      <Icon className="SearchIconB" icon={searchIcon} />
      <input className="SearchBoxB" type="text" placeholder="Search Here" name="search"></input>
      <Icon onClick={refreshPage} className="RefreshIconB" icon={refreshIcon} />
     


</div>





    <table className="usergrouptableB">
    <thead>
    <tr>
    <th>Standard&nbsp;Name</th>
    <th>Standard&nbsp;Abbreviation</th>
    <th>Created&nbsp;Date</th>
    <th>Created&nbsp;By</th>
    <th>Modified&nbsp;Date</th>
    <th>Modified&nbsp;By</th>
    <th>Edit</th>
    <th>Del</th>
    </tr>
    </thead>
    <tbody>
         {getData.map((posts)=><tr key={posts.id}>
        <td>{posts.title}</td>
        <td>{posts.userId}</td>
        <td>{posts.id}</td>
        <td>{posts.title}</td>
        <td>{posts.userId}</td>
        <td>{posts.id}</td>
        <td><Icon onClick={(e)=>{handleEdit(e,posts)}} style={{color:"#0F9D58",fontSize:"26px", cursor:"pointer"}} icon={bxsEdit} /></td>
        <td><Icon onClick={(e)=>{handleDelete(e,posts)}} style={{color:"#DB4437", fontSize:"26px",cursor:"pointer" }} icon={delete24Regular} /></td>
        
        </tr>)}
         
        </tbody>
        </table>
    </>    )
      }; 

export default Table;