import React, { useEffect, useState } from 'react';

import { Icon, InlineIcon } from '@iconify/react';
import delete24Regular from '@iconify-icons/fluent/delete-24-regular';
import bxsEdit from '@iconify-icons/bx/bxs-edit';
import refreshIcon from '@iconify-icons/el/refresh';
import searchIcon from '@iconify-icons/carbon/search';



function Table(){
const [Search,Setsearch]=useState("")
const [getData,setData]=useState([])
const [postData1,setPostData]=useState("");
const [postData2,setPostData1]=useState("");
const [check,setCheck]=useState();


function changeHandler1(event){
  setPostData(event.target.value);}


function changeHandler2(event){
  setPostData1(event.target.checked);}

const handleDelete=(e,post)=>{
  fetch("",{method:"DELETE"})
  .then(()=>alert("Deleted SuccessFully"))
}

const handleEdit=(e,post)=>{

  setPostData(post.title);
 setPostData1(post.completed);
 console.log(check);
  fetch("",{
    method:"PUT",
    headers:{'content-type':'application/json'},
    body:JSON.stringify(post)})
    .then(response=>response.json())
    .then(data=>console.log(post.id))
}

const submitHandler=(e)=>{
  e.preventDefault();
  const postData={GroupName:postData1,IsActive:postData2};
  fetch("https://jsonplaceholder.typicode.com/posts",{
    method:"POST",
    headers:{'content-type':'application/json'},
    body:JSON.stringify(postData)
  }).then(result=>result.json())
  .then((response)=>console.log(response))
}


useEffect(()=>{
  fetch("https://jsonplaceholder.typicode.com/todos")
  .then(response=>response.json())
  .then(data=>setData(data))}
  ,[]
)


const resetHandler=()=>{
  setPostData("");setPostData1("");
  setPostData1(false);
}
  





  return( <>
    <div className="mainFormK">
<form  onSubmit={submitHandler} >
  <div className="form_K">

<div>
 <p> Group Name</p> <input className="GroupNameK" id="GroupName" value={postData1} onChange={changeHandler1} type="text" name="GroupName1"/>
  </div>
  <div>
  <label for="Active">Active</label>
  <input type="checkbox" className="ActiveCheckK"  value={postData2} onChange={changeHandler2} id="Active" name="IsActive" />
  </div>
  
  <div className="btnK">
  <div>
  <button className="resetBtnK" onClick={resetHandler}>Reset</button>
  <button className="saveBtnK"  >Save</button></div></div>

</div>
</form>  
</div>




<div className="TableHeadK">
<p>Total Record</p>
<select className="GroupSelectK" id="GroupSelect" name="GroupName" >
        <option value="">Group Name</option>
        <option value=""></option>
        <option value=""></option>
      </select>
<select className="ExactlySelectK" id="ExactlySelect" name="Exactly" >
        <option value="">Exactly</option>
        <option value=""></option>
        <option value=""></option>
      </select>
      <Icon className="SearchIconK" icon={searchIcon} />
      <input className="SearchBoxK" type="text" placeholder="Search Here" name="search" value={Search} onChange={(e)=>Setsearch(e.target.value)}></input>
      <Icon className="RefreshIconK" icon={refreshIcon} />
     


</div>





    <table className="usergrouptableK">
    <thead>
    <tr>
    <th>GroupName</th>
    <th>IsActive</th>
    <th>CreatedDate</th>
    <th>CreatedBy</th>
    <th>ModifiedDate</th>
    <th>ModifiedBy</th>
    <th>Edit</th>
    <th>Del</th>
    </tr>
    </thead>
    <tbody>
         {getData.map((posts)=><tr key={posts.id}>
        <td>{posts.title}</td>
        <td>{posts.userId}</td>
        <td>{posts.id}</td>
        <td>{posts.title}</td>
        <td>{posts.userId}</td>
        <td>{posts.id}</td>
        <td><Icon onClick={(e)=>{handleDelete(e,posts)}} style={{color:"#DB4437", fontSize:"26px" }} icon={delete24Regular} /></td>
        <td><Icon onClick={(e)=>{handleEdit(e,posts)}} style={{color:"#0F9D58",fontSize:"26px"}} icon={bxsEdit} /></td>
        
        </tr>)}
         
        </tbody>
        </table>
    </>    )
      }; 

export default Table;