import React, { useEffect, useState } from 'react';

import { Icon, InlineIcon } from '@iconify/react';
import delete48Regular from '@iconify-icons/fluent/delete-24-regular';
import bxEdit from '@iconify-icons/bx/bxs-edit';
import refreshIcon from '@iconify-icons/el/refresh';
import searchIcon from '@iconify-icons/carbon/search';



function Table(){

const [getData,setData]=useState([])
const [postData1,setPostData]=useState("");
const [postData2,setPostData1]=useState("");


function changeHandler1(event){
  setPostData(event.target.value);}


function changeHandler2(event){
  setPostData1(event.target.value);}

const handleDelete=(e,post)=>{
  fetch("",{method:"DELETE"})
  .then(()=>alert("Deleted SuccessFully"))
}

const handleEdit=(e,post)=>{

  setPostData(post.title);
 setPostData1(post.id);;
  fetch("",{
    method:"PUT",
    headers:{'content-type':'application/json'},
    body:JSON.stringify(post)})
    .then(response=>response.json())
    .then(data=>console.log(post.id))
}


const submitHandler=(e)=>{
  e.preventDefault();
  const postData={GroupName:postData1,IsActive:postData2};
  fetch("https://jsonplaceholder.typicode.com/posts",{
    method:"POST",
    headers:{'content-type':'application/json'},
    body:JSON.stringify(postData)
  }).then(result=>result.json())
  .then((response)=>console.log(response))
}


useEffect(()=>{
  fetch("https://jsonplaceholder.typicode.com/todos")
  .then(response=>response.json())
  .then(data=>setData(data))}
  ,[]
)


const resetHandler=()=>{
  setPostData("");setPostData1("");
}
  
function refreshPage() {
  window.location.reload();
}





  return( <>
    <div className="mainForm33">
<form  onSubmit={submitHandler} >
  <div className="form_33">

<div>
 <p> Board Name</p> <input className="GroupName33" id="GroupName" value={postData1} onChange={changeHandler1} type="text" name="GroupName1"/>
  </div>
<div>
 <p> Board Abbreviation</p> <input className="GroupName33" id="GroupName" value={postData2} onChange={changeHandler2} type="text" name="GroupName1"/>
  </div>
  {/* <div>
  <label for="Active">Active</label>
  <input type="checkbox" className="ActiveCheckK"  value={postData2} onChange={changeHandler2} id="Active" name="IsActive" />
  </div> */}
  
  <div className="btn33">
  <div>
  <button className="resetBtn33" onClick={resetHandler}>Reset</button>
  <button className="saveBtn33"  >Save</button></div></div>

</div>
</form>  
</div>




<div className="TableHead33">
<p>Total Record</p>
<select className="GroupSelect33" id="GroupSelect" name="GroupName" >
        <option value="">Board Name</option>
        <option value="">Board Abbreviation</option>
        {/* <option value=""></option> */}
      </select>
<select className="ExactlySelect33" id="ExactlySelect" name="Exactly" >
        <option value="">Exactly</option>
        <option value=""></option>
        <option value=""></option>
      </select>
      <Icon className="SearchIcon33" icon={searchIcon} />
      <input className="SearchBox33" type="text" placeholder="Search Here" name="search"></input>
      <Icon onClick={refreshPage} className="RefreshIcon33" icon={refreshIcon} />
     


</div>





    <table className="usergrouptable33">
    <thead>
    <tr>
    <th>BoardName</th>
    <th>BoardAbbreviation</th>
    <th>CreatedDate</th>
    <th>CreatedBy</th>
    <th>ModifiedDate</th>
    <th>ModifiedBy</th>
    <th>Edit</th>
    <th>Del</th>
    </tr>
    </thead>
    <tbody>
         {getData.map((posts)=><tr key={posts.id}>
        <td>{posts.title}</td>
        <td>{posts.userId}</td>
        <td>{posts.id}</td>
        <td>{posts.title}</td>
        <td>{posts.userId}</td>
        <td>{posts.id}</td>
        <td><Icon onClick={(e)=>{handleDelete(e,posts)}} style={{color:"#DB4437", fontSize:"26px" }} icon={delete48Regular} /></td>
        <td><Icon onClick={(e)=>{handleEdit(e,posts)}} style={{color:"#0F9D58",fontSize:"26px"}} icon={bxEdit} /></td>
        
        
        </tr>)}
         
        </tbody>
        </table>
    </>    )
      }; 

export default Table;