import React from 'react';
import { useEffect, useState } from 'react';
import delete48Regular from '@iconify-icons/fluent/delete-48-regular';
import { Icon, InlineIcon } from '@iconify/react';
import bxEdit from '@iconify-icons/bx/bx-edit';
import './userMasterTableStyle.css'
import './userMasterFormStyle.css'

const UserMasterTable =()=>{
  useEffect(() => {
    fetch('https://jsonplaceholder.typicode.com/todos').then(response=>(response.json())).then((res)=>{setfinalData(res)}).catch(error=>(console.log(error)))
}, [])
  const [userState, setuserState] = useState({userCode : '', userName: '', userGroup :'', dateOfJoining:'', localAddress:'', userLanguage:'', mobileNo:'', password:'', permanentAddress:'', email:'', userPhoto:'', userLanguage:'', active:'', branch:''})
  const [finalData, setfinalData] = useState([])

    const handler=(e)=>{
        e.preventDefault()
        fetch('https://jsonplaceholder.typicode.com/posts',
        {method:'POST',headers:{'content-type': 'application/json'},
        body:JSON.stringify(userState)})
        .then(response=>(response.json()))
        .then((res)=>{console.log(res)}).catch(error=>(console.log(error)))
    }

    const changeHandler =(e)=>{
        e.preventDefault()
        setuserState({...userState, [e.target.name]:e.target.value})
    }
  const handleDelete=(e,post)=>{
  fetch("",{method:"DELETE"})
  .then(()=>alert("Deleted SuccessFully"))
  }
  const handleEdit=(e,post)=>{
    setuserState({
          userCode :post.title ,
          userName: post.userId,
          userGroup :post.id,
          dateOfJoining:post.title, 
          localAddress:post.userId,
          userLanguage:post.id,
          mobileNo:post.id,
          password:post.title,
          permanentAddress:post.userId,
          email:post.id,
          // createdby,sab teek ho jaae ga 
          // userPhoto:'',
          userLanguage:'',
          active:post.completed,
          branch:post.title})

    fetch("",{
    method:"PUT",
    headers:{'content-type':'application/json'},
    body:JSON.stringify(finalData)})
    .then(response=>response.json())
    .then(data=>console.log(post.id))
  }

   

  

    return(<>


    <div className='fullPage'>
        <form className='fullForm' onSubmit={handler} autocomplete="on">
          <div className='title'>
            Add User
          </div>
          <div className='row1'  style={{display:'inline-flex'}}>
          <div style={{display:'grid'}}>
            <label className="usermasterlable" htmlFor='userCode' required>User Code</label>
            <input className="usermasterinput" type="text" onChange={changeHandler} value={userState.userCode} name="userCode" autoFocus required />
          </div>
          <div style={{display:'grid'}}>
            <label className="usermasterlable" htmlFor="userName" required> User Name</label>
            <input className="usermasterinput" type="text" onChange={changeHandler} value={userState.userName} name="userName" required />
          </div>
          <div style={{display:'grid'}}>
            <label className="usermasterlable" htmlFor="branch" required>Branch</label>
            <select className="usermasterselect" onChange={changeHandler} value={userState.branch} name='branch' required>
              <option>Hindi</option>
              <option>English</option>
              <option>Spanish</option>
              <option>French</option>
              <option>Japanese</option>
            </select>
          </div>
          </div>

          <div className='row2' style={{display:'inline-flex'}}>
          <div style={{display:'grid'}}>
            <label className="usermasterlable" htmlFor="userGroup" required>User Group</label>
            <select className="usermasterselect" onChange={changeHandler} value={userState.userGroup} name="userGroup" required>
              <option>Choose </option>
              <option>Frontnd Developer</option>
              <option>Backend Developer</option>
              <option>Content Writter</option>
              <option>Designer</option>
              <option>Manager</option>
            </select>
          </div>
          <div style={{display:'grid'}}>
            <label className="usermasterlable" htmlFor="mobileNo" required>Mobile No</label>
            <input className="usermasterinput" type="number" name="mobileNo" onChange={changeHandler} value={userState.mobileNo} required/>
          </div>
          <div style={{display:'grid'}}>
            <label className="usermasterlable" htmlFor="password" required>Password</label>
            <input className="usermasterinput" type="password" name="password" onChange={changeHandler} value={userState.password} required/>
          </div>
          </div>

          <div className='row3' style={{display:'inline-flex'}}>
          <div style={{display:'grid'}}>
            <label className="usermasterlable" htmlFor="dateOfJoining" required>Date Of Joining </label>
            <input className="usermasterinput" type="date" onChange={changeHandler} value={userState.dateOfJoining} name="dateOfJoining" required/>
          </div>
          <div style={{display:'grid'}}>
            <label htmlFor="localAddress" className="usermasterlable" required>Local Address</label>
            <input className="usermasterinput" type="text" name="localAddress" onChange={changeHandler} value={userState.localAddress} required/>
          </div>
          <div style={{display:'grid'}}>
            <label htmlFor="permanentAddress" className="usermasterlable" required>Permanent Address</label>
            <input className="usermasterinput" type="text" name="permanentAddress" onChange={changeHandler} value={userState.permanentAddress} required/>
          </div>
          </div>
          
          <div className='row4' style={{display:'inline-flex'}}>
          <div style={{display:'grid'}}>
            <label className="usermasterlable" htmlFor='userLanguage' required>User Language</label> 
            <select className="usermasterselect" onChange={changeHandler} value={userState.userLanguage} name='userLanguage' required>
              <option>English</option>
              <option>Hindi</option>
              <option>Spanish</option>
              <option>French</option>
              <option>Japanese</option>
            </select>
          </div>
          <div style={{display:'grid'}}>
            <label htmlFor="email" className="usermasterlable" required>Email id</label>
            <input className="usermasterinput" type="email" name="email" onChange={changeHandler} value={userState.email} required/>   
          </div>
          <div style={{display:'grid'}}>
            <label htmlFor="userPhoto" className="usermasterlable" >User Photo </label>
            <input className="usermasterinput" type="file" name="userPhoto" onChange={changeHandler} value={userState.userPhoto} required/>

          </div>
          </div>

          <div className='btns'>

            <input className="usermasterinput" type="reset" style={{width: "114px", height: "48px", marginRight:"1em",backgroundColor: 'transparent'}}/>
            <input className="usermasterinput" type="submit" style={{width: "114px", height: "48px",backgroundColor: 'transparent', color:'#0F9D58', border:'2px solid #0F9D58'}}/>

          </div>

        </form>
      
     








    <div className='tableOut'>
    <table className="usergrouptable" >
              <thead>
                <tr>
                  <th>User Code</th>
                  <th>User Name</th>
                  <th>User Group</th>
                  <th>Mobile Number</th>
                  <th>Email Id</th>
                  <th>Branch</th>
                  <th>CreatedDate</th>
                  <th>CreatedBy</th>
                  <th>ModifiedDate</th>
                  <th>ModifiedBy</th>
                  <th>Edit</th>
                  <th>Del</th>
                </tr>
              </thead>
              <tbody>
{finalData.map((posts)=><tr key={posts.id}>
        <td>{posts.title}</td>
        <td>{posts.userId}</td>
        <td>{posts.id}</td>
        <td>{posts.title}</td>
        <td>{posts.userId}</td>
        <td>{posts.id}</td>
        <td>{posts.id}</td>
        <td>{posts.title}</td>
        <td>{posts.userId}</td>
        <td>{posts.id}</td>
        <td><Icon onClick={(e)=>{handleEdit(e,posts)}} style={{color:"#0F9D58",fontSize:"26px"}} icon={bxEdit} /></td>
        <td><Icon onClick={(e)=>{handleDelete(e,posts)}} style={{color:"#DB4437", fontSize:"26px" }} icon={delete48Regular} /></td>
        
        </tr>)}
              </tbody>
            </table>
            </div>
            </div>
    </>)
}

export default UserMasterTable;